@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*, *:before, *:after {
  box-sizing: border-box;
}

body {
  font-family: 'Source Sans Pro';
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.app__container {
  width: 1000px;
  margin: 0 auto;
}

.app__header {
  margin-bottom: 3rem;
}

.app__header-title {
  font-weight: 900;
  font-size: 2.5rem;
  letter-spacing: .05rem;
}

.app__header-subtitle {
  font-size: 1.2rem;
  font-weight: 300;
  font-style: italic;
  letter-spacing: normal;
}

.builder-controls__name{font-size:1.6rem;margin-bottom:1.2rem;font-weight:900}.builder-controls__child-box{margin-bottom:.6rem}

.grid-renderer__container{position:relative}.grid-renderer__container:before{content:"";display:block;padding-top:100%}.grid-renderer__content{position:absolute;top:0;right:0;bottom:0;left:0;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.grid-renderer__row{margin:0 -4px 8px;display:-webkit-flex;display:flex;-webkit-flex:1 1;flex:1 1}.grid-renderer__row:last-of-type{margin-bottom:0}.grid-renderer__cell{margin:0 4px;background:#efefef;-webkit-flex:1 1;flex:1 1;position:relative;cursor:pointer;border-radius:.1rem;z-index:2;border:1px solid transparent}.grid-renderer__cell:after{content:"";position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.grid-renderer__cell:hover{border-color:rgba(0,0,0,0.1)}.grid-renderer__cell__wall{background-color:#4a4a4a}.grid-renderer__cell__start,.grid-renderer__cell__finish{background-color:#fff;border-color:rgba(0,0,0,0.05)}.grid-renderer__cell__start:after,.grid-renderer__cell__finish:after{font-weight:700;font-size:1.4rem;color:#4a4a4a;font-style:italic}.grid-renderer__cell__start:after{content:"S"}.grid-renderer__cell__finish:after{content:"F"}.grid-renderer__cell__path:after{width:60%;text-align:center;vertical-align:middle;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;height:60%;background:#ABEBD2;border-radius:2px}

.control-wrap__container{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.control-wrap__name{color:#4a4a4a}.control-wrap__proxy-box{margin-left:auto}

.number-selector__container{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin:0 -.75rem}.number-selector__control{margin:0 .75rem;color:#4a4a4a;cursor:pointer;font-size:1.3rem}.number-selector__display{font-size:.9rem;width:3rem;border:1px solid #ccc;font-style:italic;text-align:center;padding:.2rem 0;border-radius:.2rem}

.array-selector__container{display:-webkit-flex;display:flex;border:1px solid #ccc;border-radius:.2rem;overflow:hidden}.array-selector__item{-webkit-flex-shrink:0;flex-shrink:0;padding:.2rem .5rem;border-right:1px solid #ccc;font-weight:400;font-size:.9rem;cursor:pointer;color:#4a4a4a}.array-selector__item:hover{background:rgba(0,0,0,0.05)}.array-selector__item:last-of-type{border-right:none}.array-selector__item__selected,.array-selector__item__selected:hover{background:rgba(0,0,0,0.1)}

.builder__wrap{display:-webkit-flex;display:flex;margin:0 -20px}.builder__left-side{padding:0 20px;width:600px;position:relative}.builder__left-side:before{content:"";display:block;position:absolute;top:-20px;right:0;bottom:-20px;left:0;background:#fff;box-shadow:0 32px 128px 0 rgba(0,0,0,0.2),0 4px 16px 0 rgba(0,0,0,0.1);border-radius:.2rem}.builder__right-side{-webkit-flex:1 1;flex:1 1;padding:10px 20px;position:relative}.builder__right-side:before{content:"";display:block;position:absolute;top:0;right:0;bottom:0;left:0;background:#fff;box-shadow:0 16px 64px 0 rgba(0,0,0,0.1),0 2px 8px 0 rgba(0,0,0,0.05);z-index:-1;border-radius:.2rem}.builder__content{position:relative}

